import { combineReducers } from "redux";

// USER
const UserReducer = (state = {}, { type, payload }) => {
  switch (type) {
    case "SET_USER":
      return payload;
    case "CLEAR_USER":
      return {};
    default:
      return state;
  }
};

const x = {
  user: UserReducer,
};

export default combineReducers(x);
